import React, { Component } from "react";

class RekapBonCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-md-12">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-4 col-md-4">
                  <label htmlFor="">Tanggal</label>
                  <input
                    type="date"
                    name="tanggal"
                    className="form-control"
                    onChange={this.onChange("tanggal")}
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <label htmlFor="">Nama Toko</label>
                  <input
                    type="text"
                    name="namaToko"
                    className="form-control"
                    onChange={this.onChange("namaToko")}
                  />
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="">Customer</label>
                  <input
                    type="text"
                    name="customer"
                    className="form-control"
                    onChange={this.onChange("customer")}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default RekapBonCustomer;
